import * as React from 'react';
import Card from '../Card';
import Section from '../Section';
import { Box } from '@chakra-ui/core';

const icons = ['ideas', 'structure', 'rent'];

export default function Description({ data, ...props }) {
  return (
    <Section display="flex" flexWrap="wrap" {...props}>
      <Box mx={[2, 8, 16]}>
        {data.map((card, index) => (
          <Card key={card.id} card={card} icon={icons[index]} index={index} />
        ))}
      </Box>
    </Section>
  );
}
